.background-image {
}

html {
  margin: 0;
  padding: 0px;
}
.background {
  min-width: 75vw;
  min-height: 100vh;
  background-image: url('../assets/img/index-backgorund-img.png');
  /* background-size: cover; */
  background-repeat: no-repeat;
}

.div-buttons {
  margin-top: 200px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.bio {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-left: 10px;
  padding-right: 10px;
  font-family: 'Roboto Mono', monospace;
}
@media (min-height: 840px) {
  .background {
    max-width: fit-content;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: 50%;
  }
}
